@import url('https://fonts.googleapis.com/css?family=Josefin+Sans|Nunito+Sans:300,400,700,900&display=swap');

html,
body {
    margin: 0;
    padding: 0;
    font: 13px/1.4em 'Nunito Sans', 'Helvetica', 'Arial', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
p,
ul,
ol,
li {
    margin: 0;
    padding: 0;
    font-size: 100%;
}

